.note {
    background: #DAF6FA;
    border-radius: 7px;
    box-shadow: 0 2px 5px #9cc6ee;
    padding: 10px;
    width: 240px;
    margin: 16px;
    float: left;
}
.note h1 {
    font-size: 1.1em;
    margin-bottom: 6px;
}
.note p {
    font-size: 1.1em;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
}
.note button{
    position: relative;
    float: right;
    margin-right: 10px;
    color: #0D5D68;
    border: none;
    width: 36px;
    height: 34px;
    cursor: pointer;
    outline: none;
}