form.create-note{
    position: relative;
    width: 480px;
    margin: 40px auto 20px auto;
    background: #ffffff;
    padding: 3px;
    border-radius: 7px;
    box-shadow: 0 2px 5px rgb(138,137,137);
}
form.create-note input,
form.create-note textarea{
    width: 100%;
    border: none;
    padding: 4px;
    outline: none;
    font-size: 1.2em;
    font-family: inherit;
    resize: none;
}
form.create-note button{
    position: absolute;
    right: 15px;
    bottom: 4px;
    background: #3C919D;
    color: #ffffff;
    border: none;
    border-radius: 50%;
    width: 36px;
    height: 14px;
    box-shadow: 0 3px 3px rgba(0,0,0, 3);
    cursor: pointer;
    outline: none;
}