@import url(https://fonts.googleapis.com/css2?family=McLaren&display=swap);
@import url(https://fonts.googleapis.com/css2?family=McLaren&display=swap);

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html{
    font-family: 'McLaren', cursive;
}

body{
    background: #ffffff;
    padding: 0 16px;
}
form.create-note{
    position: relative;
    width: 480px;
    margin: 40px auto 20px auto;
    background: #ffffff;
    padding: 3px;
    border-radius: 7px;
    box-shadow: 0 2px 5px rgb(138,137,137);
}
form.create-note input,
form.create-note textarea{
    width: 100%;
    border: none;
    padding: 4px;
    outline: none;
    font-size: 1.2em;
    font-family: inherit;
    resize: none;
}
form.create-note button{
    position: absolute;
    right: 15px;
    bottom: 4px;
    background: #3C919D;
    color: #ffffff;
    border: none;
    border-radius: 50%;
    width: 36px;
    height: 14px;
    box-shadow: 0 3px 3px rgba(0,0,0, 3);
    cursor: pointer;
    outline: none;
}
footer {
    position: absolute;
    text-align: center;
    bottom: 0;
    width: 100%;
    height: 2.5rem;
}
footer p {
    color: #0F1683;
}
header {
    background-color:#08C9E3;
    margin: auto -16px;
    padding: 10px 17px;
    box-shadow: 0 0 30px 0 rgba(0,0,0 alpha);
}

header h1{
    color: #ffffff;
    font-family: 'McLaren', cursive;
    font-weight: 200;
}




.note {
    background: #DAF6FA;
    border-radius: 7px;
    box-shadow: 0 2px 5px #9cc6ee;
    padding: 10px;
    width: 240px;
    margin: 16px;
    float: left;
}
.note h1 {
    font-size: 1.1em;
    margin-bottom: 6px;
}
.note p {
    font-size: 1.1em;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
}
.note button{
    position: relative;
    float: right;
    margin-right: 10px;
    color: #0D5D68;
    border: none;
    width: 36px;
    height: 34px;
    cursor: pointer;
    outline: none;
}
